import { TaskProcessOption, TaskStatusOption } from "../constants/taskOptions";

export type TaskProcess = TaskProcessOption;
export type TaskStatus = TaskStatusOption;
export type EffortUnit = "MH" | "MD" | "MM";
export type VisibleField =
  | "name"
  | "start"
  | "end"
  | "process"
  | "assignee"
  | "plannedStart"
  | "plannedEnd"
  | "plannedEffort"
  | "actualEffort"
  | "status";

export type CellCommitTrigger = "enter";

export type CellCommitPayload = {
  rowId: string;
  columnId: VisibleField;
  value: string;
  trigger: CellCommitTrigger;
};

export type ColumnState = {
  id: VisibleField;
  label: string;
  width: number;
  minWidth: number;
  visible: boolean;
};

export type ColumnsState = ColumnState[];

export enum ViewMode {
  Hour = "Hour",
  QuarterDay = "Quarter Day",
  HalfDay = "Half Day",
  Day = "Day",
  /** ISO-8601 week */
  Week = "Week",
  Month = "Month",
  QuarterYear = "QuarterYear",
  Year = "Year",
}
export type TaskType = "task" | "milestone" | "project";
export interface Task {
  id: string;
  type: TaskType;
  name: string;
  start: Date;
  end: Date;
  /**
   * From 0 to 100
   */
  progress: number;
  styles?: {
    backgroundColor?: string;
    backgroundSelectedColor?: string;
    progressColor?: string;
    progressSelectedColor?: string;
  };
  isDisabled?: boolean;
  project?: string;
  dependencies?: string[];
  hideChildren?: boolean;
  displayOrder?: number;
  process?: TaskProcess;
  assignee?: string;
  plannedStart?: Date;
  plannedEnd?: Date;
  plannedEffort?: number;
  actualEffort?: number;
  status?: TaskStatus;
}

export interface EventOption {
  /**
   * Time step value for date changes.
   */
  timeStep?: number;
  /**
   * Invokes on bar select on unselect.
   */
  onSelect?: (task: Task, isSelected: boolean) => void;
  /**
   * Invokes on bar double click.
   */
  onDoubleClick?: (task: Task) => void;
  /**
   * Invokes on bar click.
   */
  onClick?: (task: Task) => void;
  /**
   * Invokes on end and start time change. Chart undoes operation if method return false or error.
   */
  onDateChange?: (
    task: Task,
    children: Task[]
  ) => void | boolean | Promise<void> | Promise<boolean>;
  /**
   * Invokes on progress change. Chart undoes operation if method return false or error.
   */
  onProgressChange?: (
    task: Task,
    children: Task[]
  ) => void | boolean | Promise<void> | Promise<boolean>;
  /**
   * Invokes on delete selected task. Chart undoes operation if method return false or error.
   */
  onDelete?: (task: Task) => void | boolean | Promise<void> | Promise<boolean>;
  /**
   * Invokes on expander on task list
   */
  onExpanderClick?: (task: Task) => void;
  /**
   * Invokes when task list fields are updated (process, status, planned dates, assignee, effort).
   */
  onTaskUpdate?: (taskId: string, updatedFields: Partial<Task>) => void;
  /**
   * Invokes when a task list cell edit is committed.
   * Notification only; the library does not update tasks or UI.
   * Host must validate/persist, update tasks, and pass new props for rerender.
   */
  onCellCommit?: (payload: CellCommitPayload) => Promise<void>;
}

export interface DisplayOption {
  viewMode?: ViewMode;
  viewDate?: Date;
  preStepsCount?: number;
  /**
   * Specifies the month name language. Able formats: ISO 639-2, Java Locale
   */
  locale?: string;
  rtl?: boolean;
}

export interface StylingOption {
  headerHeight?: number;
  columnWidth?: number;
  listCellWidth?: string;
  rowHeight?: number;
  ganttHeight?: number;
  barCornerRadius?: number;
  handleWidth?: number;
  fontFamily?: string;
  fontSize?: string;
  /**
   * How many of row width can be taken by task.
   * From 0 to 100
   */
  barFill?: number;
  barProgressColor?: string;
  barProgressSelectedColor?: string;
  barBackgroundColor?: string;
  barBackgroundSelectedColor?: string;
  projectProgressColor?: string;
  projectProgressSelectedColor?: string;
  projectBackgroundColor?: string;
  projectBackgroundSelectedColor?: string;
  milestoneBackgroundColor?: string;
  milestoneBackgroundSelectedColor?: string;
  arrowColor?: string;
  arrowIndent?: number;
  todayColor?: string;
  visibleFields?: VisibleField[];
  effortDisplayUnit?: EffortUnit;
  enableColumnDrag?: boolean;
  TooltipContent?: React.FC<{
    task: Task;
    fontSize: string;
    fontFamily: string;
    effortDisplayUnit?: EffortUnit;
  }>;
  TaskListHeader?: React.FC<{
    headerHeight: number;
    rowWidth: string;
    fontFamily: string;
    fontSize: string;
    visibleFields: VisibleField[];
    columnsState?: ColumnsState;
    setColumnsState?: React.Dispatch<React.SetStateAction<ColumnsState>>;
    enableColumnDrag?: boolean;
  }>;
  TaskListTable?: React.FC<{
    rowHeight: number;
    rowWidth: string;
    fontFamily: string;
    fontSize: string;
    locale?: string;
    tasks: Task[];
    selectedTaskId: string;
    /**
     * Sets selected task by id
     */
    setSelectedTask: (taskId: string) => void;
    onExpanderClick: (task: Task) => void;
    visibleFields: VisibleField[];
    onUpdateTask?: (taskId: string, updatedFields: Partial<Task>) => void;
    effortDisplayUnit: EffortUnit;
    columnsState?: ColumnsState;
  }>;
}

export interface GanttProps extends EventOption, DisplayOption, StylingOption {
  tasks: Task[];
}
